function onChange(elem){
    console.log(elem.currentTarget.value);
    
    /*set  */
   switch(elem.currentTarget.value) {
     case 'Columbian':
          /*set units - packs*/
       setFields({label:"Select (Columbian had selected) :", placeholder:"Kg", id:"mySelect"});
    break;
    case 'Brazilian':
       setFields({label:"Select (Brazilian had selected)", placeholder:"Kg", id:"mySelect"});
    break;
    case 'Indian':
       setFields({label:"Select (Indian had selected)", placeholder:"Kg", id:"mySelect"});
    break;
    default:
      console.log('NO appropriate variants((');
   }
 
}

function setFields (x={label:"", placeholder:"",id:""}) {
    let input = document.querySelector(`span[id="${x.id}"]`);
    let label = document.querySelector(`label[for="${x.id}"]`);
    if (label) {
        label.innerText = x.label;
    } if (input) {
        input.innerText = x.placeholder;
    } else {
        console.log("Error: node not found");
    }
}

export {onChange}