import "./beans.css"
import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./custom.scss";
import x1 from "../favicon.ico";
import  * as sw1 from './autochange.js';
import refimage from "../bckg-back.svg";
window.bootstrap = import("bootstrap/dist/js/bootstrap.bundle");
///download favicon
let ic = document.querySelectorAll("link[type='image/x-icon']");
if(ic.length > 0) {
  console.log('Icon has loaded successfully!');
  ic[0].href = x1;
}


// Example starter JavaScript for disabling form submissions if there are invalid fields
(function () {
  'use strict'

  // Fetch all the forms we want to apply custom Bootstrap validation styles to
  var form = document.querySelector('.myform1')

  // Loop over them and prevent submission
 
      form.addEventListener('click', function (event) {
        if (!form.checkValidity()) {
           event.preventDefault()
           event.stopPropagation()
        }
        form.classList.add('was-validated')
      }, false);
    
})()


window.onload = function () {

  
  let spinner = document.querySelector('.spinner-border');
  spinner.style.display = "none";
  const refback = document.querySelector('.refimage');
  refback.src=refimage;
  
  let select = document.querySelector("select[id='mySelect']");
  let btn = document.querySelector(".mybtn");
  let form = document.querySelector(".myform1");

btn.addEventListener('click', onClick, false);
select.addEventListener('change',sw1.onChange,false);


function onClick (event) {
    if (!form.checkValidity()) {
      return;
    }

  form.classList.add('was-validated')
  spinner.style.display = "inline-block";
  return new Promise((resolve)=>{
    setTimeout(()=>{
      spinner.style.display = "none";
      resolve('ok!');
    },2000)
  })
  .then((x)=>{
    document.location.assign("http://localhost:9000/index.html");
  })
}


}
